












































import Vue from 'vue';
import moment from 'moment';

export default Vue.extend({
  name: 'MessageBox',
  filters: {
    // 将日期过滤为 hour:minutes
    time: (date: number) => moment(date).format('YYYY-MM-DD HH:mm:ss'),
  },
  computed: {
    isSelf(): boolean {
      return this.message.iSender !== this.$store.state.profile.user.pid;
    },
  },
  props: {
    message: Object,
  },
});
